/* -------------------- index.html file ------------------------- */

body {
  margin: 0;
  font-family: "Espuma Pro Medium";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

html {
  height: 100%;
  width: 100%;
}

/* ------------------------- General ------------------------- */

.odd {
  color: #00ffbb;
}

.centered {
  margin-left: auto;
  margin-right: auto;
  float: none;
}

.section-title {
  text-align: center;
  text-decoration: solid;
  text-shadow: 0 0 2px;
  color: #d6d7e1;
  letter-spacing: 7px;
  text-transform: uppercase;
  font-weight: 900;
}

.line {
  display: block;
  width: 120px;
  height: 2px;
  margin-top: 2rem;
  margin-bottom: 2rem;
  background: #03e9f4;
}

.center-div {
  justify-content: center;
}

.in-middle-about {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
}

/* ------------------------- Navbar -------------------------- */

.bottom-nav {
  position: absolute;
  bottom: 0;
}

.top-nav {
  position: fixed;
  top: 0;
}

.nav {
  text-align: center;
  width: 100%;
  box-shadow: 0 5px 5px 0px rgba(0, 0, 0, 0.2);
  background: #092756;
  background: -moz-linear-gradient(
      0% 100%,
      rgba(104, 128, 138, 0.4) 10%,
      rgba(138, 114, 76, 0) 40%
    ),
    -moz-linear-gradient(top, rgba(57, 173, 219, 0.25) 0%, rgba(42, 60, 87, 0.4)
          100%),
    -moz-linear-gradient(#670d10 0%, #092756 100%);
  background: -webkit-radial-gradient(
      0% 100%,
      ellipse cover,
      rgba(104, 128, 138, 0.4) 10%,
      rgba(138, 114, 76, 0) 40%
    ),
    -webkit-linear-gradient(top, rgba(57, 173, 219, 0.25) 0%, rgba(
            42,
            60,
            87,
            0.4
          )
          100%),
    -webkit-linear-gradient(-45deg, #670d10 0%, #092756 100%);
  background: -o-radial-gradient(
      0% 100%,
      ellipse cover,
      rgba(104, 128, 138, 0.4) 10%,
      rgba(138, 114, 76, 0) 40%
    ),
    -o-linear-gradient(top, rgba(57, 173, 219, 0.25) 0%, rgba(42, 60, 87, 0.4)
          100%),
    -o-linear-gradient(-45deg, #670d10 0%, #092756 100%);
  background: -ms-radial-gradient(
      0% 100%,
      ellipse cover,
      rgba(104, 128, 138, 0.4) 10%,
      rgba(138, 114, 76, 0) 40%
    ),
    -ms-linear-gradient(top, rgba(57, 173, 219, 0.25) 0%, rgba(42, 60, 87, 0.4)
          100%),
    -ms-linear-gradient(-45deg, #670d10 0%, #092756 100%);
  background: linear-gradient(
      to bottom,
      rgba(57, 173, 219, 0.25) 0%,
      rgba(42, 60, 87, 0.4) 100%
    ),
    linear-gradient(135deg, #670d10 0%, #092756 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3E1D6D', endColorstr='#092756', GradientType=1);
  z-index: 1;
  padding-top: 10px;
}

.nav ul li {
  list-style-type: none;
  display: inline-block;
  padding: 10px;
}

.nav ul li .nav-link:hover {
  text-decoration: none;
  color: #03e9f4;
}

.nav ul li .nav-link:active {
  color: #03e9f4;
  text-decoration: solid;
}

.nav ul li .nav-link {
  text-decoration: none;
  color: #ccc;
  text-transform: uppercase;
  font-size: 14px;
  border-style: none;
}

.nav ul li .nav-resume {
  text-decoration: none;
  font-size: 14px;
  color: #03e9f4;
  text-transform: uppercase;
  border: 1px solid #03e9f4;
}

.nav ul li .nav-resume:hover {
  background-color: rgba(3, 233, 244, 0.1);
}

.contact-email:hover {
  background-color: rgba(3, 233, 244, 0.1);
}

@media (max-width: 768px) {
  .nav {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }
}

/* ------------------------- Home ---------------------------- */

