.tape {
  background-color: hsla(0, 0%, 100%, 0.8);
  box-shadow: inset 0 0 1em 0.5em hsla(0, 0%, 100%, 0.1);
  height: 3em;
  top: -22px;
  left: 0;
  right: 0;
  color: #072142;
  font-size: 20px;
  font-weight: 900;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: auto;
  padding-bottom: auto;
  transform: rotate(1deg);
  max-width: 180px;
  -webkit-filter: drop-shadow(0 1px 0.7px hsla(0, 0%, 0%, 0.3));
}

.tape:after,
.tape:before {
  background-size: 0.4em 0.4em;
  bottom: 0;
  content: "";
  position: absolute;
  top: 0;
  width: 0.2em;
}

.tape:after {
  background-image: linear-gradient(
      45deg,
      transparent 50%,
      hsla(0, 0%, 100%, 0.3) 50%
    ),
    linear-gradient(-45deg, transparent 50%, hsla(0, 0%, 100%, 0.3) 50%);
  background-position: 0 100%;
  left: -0.2em;
}

.tape:before {
  background-image: linear-gradient(
      135deg,
      transparent 50%,
      hsla(0, 0%, 100%, 0.3) 50%
    ),
    linear-gradient(-135deg, transparent 50%, hsla(0, 0%, 100%, 0.3) 50%);
  background-position: 100% 100%;
  right: -0.2em;
}

