* {
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  font-family: "Espuma Pro Medium";
  background: #092756;
  background: linear-gradient(
      to bottom,
      rgba(57, 173, 219, 0.25) 0%,
      rgba(42, 60, 87, 0.4) 100%
    ),
    linear-gradient(135deg, #670d10 0%, #092756 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3E1D6D', endColorstr='#092756', GradientType=1);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* 1st part of last bg
-webkit-radial-gradient(
      0% 100%,
      ellipse cover,
      rgba(104, 128, 138, 0.4) 10%,
      rgba(138, 114, 76, 0) 40%
    ), */

.login {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -150px 0 0 -150px;
  width: 300px;
  height: 300px;
}

.login h1 {
  color: #fff;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  letter-spacing: 1px;
  text-align: center;
}

input {
  width: 100%;
  margin-bottom: 10px;
  background: rgba(0, 0, 0, 0.3);
  border: none;
  outline: none;
  padding: 10px;
  font-size: 13px;
  color: #fff;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  box-shadow: inset 0 -5px 45px rgba(100, 100, 100, 0.2),
    0 1px 1px rgba(255, 255, 255, 0.2);
  transition: box-shadow 0.5s ease;
}

input:focus,
textarea:focus {
  box-shadow: inset 0 -5px 45px rgba(100, 100, 100, 0.4),
    0 1px 1px rgba(255, 255, 255, 0.2);
}

textarea {
  width: 100%;
  height: 100px;
  margin-bottom: 10px;
  background: rgba(0, 0, 0, 0.3);
  border: none;
  outline: none;
  padding: 10px;
  font-size: 13px;
  color: #fff;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  box-shadow: inset 0 -5px 45px rgba(100, 100, 100, 0.2),
    0 1px 1px rgba(255, 255, 255, 0.2);
  transition: box-shadow 0.5s ease;
}

button[type="submit"] {
  width: 100%;
}

form {
  padding: 2rem;
  margin-left: auto;
  margin-right: auto;
}

/* -------------------------- Large Devices Only ------------------------*/

@media (min-width: 992px) {
  form {
    width: 30%;
  }
}

/********** Medium devices only **********/

@media (min-width: 768px) and (max-width: 991px) {
  /**************** home ***********/
  .line1 {
    font-size: 53px;
  }
  .line2 {
    font-size: 35px;
  }
  .line3 {
    font-size: 25px;
  }
  .contents {
    padding-top: 40%;
  }
  .display {
    display: block;
  }
  /**************** contact ***********/
  form {
    width: 30%;
  }
  /**************** about ***********/
  .about-main {
    display: block;
  }
  .card-text {
    margin-left: 0rem;
    padding-left: 2rem;
    padding-right: 2rem;
    font-size: 23px;
    width: 100%;
    text-align: justify;
  }
  img {
    margin-top: 3rem;
    width: 40%;
    height: 50%;
    margin-right: 5rem;
    justify-content: center;
  }

  /*********************** skills *******/
  .skills {
    margin-bottom: 90rem;
  }

  .container-skills {
    width: 90%;
  }
  .skill-set__icon {
    width: 10.6382%;
  }

  /**************** projects ************/

  .projects {
    margin-top: 40rem;
    margin-bottom: 140rem;
  }

  /************* experience ******************/
  .experience {
    margin-bottom: 90rem;
    margin-top: 35rem;
  }
}

/********** Small devices only **********/

@media (min-width: 0px) and (max-width: 767px) {
  /*********** home **************/
  .line1 {
    font-size: 38px;
  }
  .line2 {
    font-size: 20px;
  }
  .line3 {
    font-size: 17px;
  }
  .contents {
    padding-top: 40%;
  }
  .display {
    display: block;
  }
  /**************** contact ***********/
  form {
    width: 100%;
  }
  /************** about *************/
  .about-main {
    display: block;
  }
  .card-text {
    margin: 0;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 16px;
    width: 100%;
  }
  img {
    width: 40%;
    margin-top: 3rem;
    height: 56%;
    margin-right: 5rem;
  }
  .skills {
    margin-bottom: 128rem;
  }

  .container-skills {
    width: 90%;
  }
  .skill-set__icon {
    width: 17.6382%;
  }

  /************* education ******************/
  .education {
    margin-top: 20rem;
  }

  /**************** projects ************/

  .projects {
    margin-top: 40rem;
    margin-bottom: 150rem;
  }

  /************* experience ******************/
  .experience {
    margin-bottom: 140rem;
    margin-top: 95rem;
  }
}

/************** small height devices ***************/

@media (min-height: 569px) and (max-height: 737px) and (min-width: 0px) and (max-width: 991px) {
  .card-text {
    font-size: 13px;
  }
}

@media (min-height: 0) and (max-height: 568px) {
  .card-text {
    font-size: 11px;
  }
}

